body {
  font-family: "Playfair Display", serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.7;
  color: #030000;
  background: #fff;
}

#page {
  position: relative;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  transition: 0.5s;
}

.offcanvas #page {
    overflow: hidden;
    position: absolute;
  }
  .offcanvas #page:after {
    transition: 2s;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 101;
    background: rgba(235, 157, 41, 0.7);
    content: "";
  }
  
  a {
    color: #000000;
    transition: 0.5s;
  }
  a:hover, a:active, a:focus {
    color: #000000;
    outline: none;
    text-decoration: none;
  }



.fh5co-nav {
    padding: 18px 0;
    background: #fff;
  }
  @media screen and (max-width: 768px) {
    .fh5co-nav {
      padding: 20px 0;
    }
  }
  .fh5co-nav #fh5co-logo {
    font-size: 20px;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
    font-weight: bold;
    font-family: "Montserrat", Arial, sans-serif;
  }
  .fh5co-nav a {
    padding: 5px 10px;
    color: #000;
  }
  @media screen and (max-width: 768px) {
    .fh5co-nav .menu-1, .fh5co-nav .menu-2 {
      display: none;
    }
  }
  .fh5co-nav .menu-2 li.search {
    float: left;
    width: 53%;
    font-family: "Playfair Display", serif;
    transition: 0.3s;
    padding: 0;
    margin: -5px 0 0 0;
  }
  .fh5co-nav .menu-2 li input[type=text] {
    padding: 6px 10px;
    width: 40%;
    border: none;
    float: right;
    font-size: 13px;
    margin-top: 0px;
    transition: 0.3s;
  }
  .fh5co-nav .menu-2 li input[type=text]:focus {
    width: 100%;
    outline: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
  .fh5co-nav .menu-2 li button {
    margin-top: 4px;
    padding: 5px;
    background: transparent;
    border: none;
  }
  .fh5co-nav .menu-2 li button i {
    color: rgba(0, 0, 0, 0.5);
  }
  .fh5co-nav .menu-2 li button:hover, .fh5co-nav .menu-2 li button:focus {
    background: transparent !important;
  }
  .fh5co-nav .menu-2 li button:hover i, .fh5co-nav .menu-2 li button:focus i {
    color: black;
  }
  .fh5co-nav .menu-2 li.shopping-cart {
    float: right;
  }
  .fh5co-nav .menu-2 li a.cart {
    color: black;
  }
  .fh5co-nav .menu-2 li a.cart span {
    position: relative;
  }
  .fh5co-nav .menu-2 li a.cart span small {
    position: absolute;
    top: -20px;
    right: -15px;
    padding: 10px 6px;
    line-height: 0;
    font-size: 11px;
    background: #d1c286;
    color: white;
    border-radius: 50%;
  }
  .fh5co-nav ul {
    padding: 0;
    margin: 0;
  }
  .fh5co-nav ul li {
    padding: 0;
    margin: 0;
    list-style: none;
    display: inline-block;
    font-family: "Montserrat", Arial, sans-serif;
  }
  .fh5co-nav ul li a {
    font-size: 13px;
    padding: 30px 10px;
    text-transform: uppercase;
    color: rgba(0, 0, 0, 0.5);
    transition: 0.5s;
  }
  .fh5co-nav ul li a:hover, .fh5co-nav ul li a:focus, .fh5co-nav ul li a:active {
    color: black;
  }
  .fh5co-nav ul li a.cart {
    color: black;
  }
  .fh5co-nav ul li a.cart span {
    position: relative;
  }
  .fh5co-nav ul li a.cart span small {
    position: absolute;
    top: -20px;
    right: -15px;
    padding: 10px 6px;
    line-height: 0;
    font-size: 11px;
    background: #d1c286;
    color: white;
    border-radius: 50%;
  }
  .fh5co-nav ul li.has-dropdown {
    position: relative;
  }
  .fh5co-nav ul li.has-dropdown .dropdown {
    width: 150px;
    box-shadow: 0px 14px 33px -9px rgba(0, 0, 0, 0.75);
    z-index: 1002;
    visibility: hidden;
    opacity: 0;
    position: absolute;
    top: 40px;
    left: 0;
    text-align: left;
    background: #000;
    padding: 20px;
    border-radius: 4px;
    transition: 0s;
  }
  .fh5co-nav ul li.has-dropdown .dropdown:before {
    bottom: 100%;
    left: 40px;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-bottom-color: #000;
    border-width: 8px;
    margin-left: -8px;
  }
  .fh5co-nav ul li.has-dropdown .dropdown li {
    display: block;
    margin-bottom: 7px;
  }
  .fh5co-nav ul li.has-dropdown .dropdown li:last-child {
    margin-bottom: 0;
  }
  .fh5co-nav ul li.has-dropdown .dropdown li a {
    padding: 2px 0;
    display: block;
    color: #000000;
    line-height: 1.2;
    text-transform: none;
    font-size: 15px;
  }
  .fh5co-nav ul li.has-dropdown .dropdown li a:hover {
    color: #fff;
  }
  .fh5co-nav ul li.has-dropdown:hover a, .fh5co-nav ul li.has-dropdown:focus a {
    color: #000;
  }
  .fh5co-nav ul li.active > a {
    color: #000 !important;
  }
  .fh5co-nav ul li .form-group {
    border: 1px solid red;
    width: 100%;
  }

#fh5co-hero {
    min-height: 420px;
